export function timeFix () {
  const time = new Date()
  const hour = time.getHours()
  return hour < 9 ? '早上好' : hour <= 11 ? '上午好' : hour <= 13 ? '中午好' : hour < 20 ? '下午好' : '晚上好'
}

export function welcome () {
  const arr = ['休息一会儿吧', '准备吃什么呢?', '要不要打一把 DOTA', '我猜你可能累了']
  const index = Math.floor(Math.random() * arr.length)
  return arr[index]
}

/**
 * 触发 window.resize
 */
export function triggerWindowResizeEvent () {
  const event = document.createEvent('HTMLEvents')
  event.initEvent('resize', true, true)
  event.eventType = 'message'
  window.dispatchEvent(event)
}

export function handleScrollHeader (callback) {
  let timer = 0

  let beforeScrollTop = window.pageYOffset
  callback = callback || function () {}
  window.addEventListener(
    'scroll',
    event => {
      clearTimeout(timer)
      timer = setTimeout(() => {
        let direction = 'up'
        const afterScrollTop = window.pageYOffset
        const delta = afterScrollTop - beforeScrollTop
        if (delta === 0) {
          return false
        }
        direction = delta > 0 ? 'down' : 'up'
        callback(direction)
        beforeScrollTop = afterScrollTop
      }, 50)
    },
    false
  )
}

export function isIE () {
  const bw = window.navigator.userAgent
  const compare = (s) => bw.indexOf(s) >= 0
  const ie11 = (() => 'ActiveXObject' in window)()
  return compare('MSIE') || ie11
}

/**
 * Remove loading animate
 * @param id parent element id or class
 * @param timeout
 */
export function removeLoadingAnimate (id = '', timeout = 1500) {
  if (id === '') {
    return
  }
  setTimeout(() => {
    document.body.removeChild(document.getElementById(id))
  }, timeout)
}

/**
 * 防抖
 *  **/
export function debounce () {
  let timeout = null
  return function (fn, time) {
    // 因为需要在指定之后后才能运行，需要清除 clearTimeout
    clearTimeout(timeout)
    timeout = setTimeout(() => {
      fn.apply(this, arguments)
    }, time || 500)
  }
}

/**
 * 节流
 *  **/
export function throttle (fn, time) {
  let canRun = true
  return function () {
    if (!canRun) return
    canRun = false
    setTimeout(() => {
      fn.apply(this, arguments)
      canRun = true
    }, time || 500)
  }
}

/**
 * 转换Key
 * @param {Array|Object} source
 * @param {Object} keyMap
 * @param {Boolean} isDeep 是否深度
 * @return {Object|Array}
 */
export const convertKey = (source, keyMap, isDeep) => {
  if (!['[object Array]', '[object Object]'].includes(Object.prototype.toString.call(source))) {
    throw new TypeError('The first argument should be either an object or an array！')
  }
  if (Object.prototype.toString.call(keyMap) !== '[object Object]') {
    throw new TypeError('The parameter keyMap should be an object!')
  }
  const res = source instanceof Array ? [] : {}
  if (source instanceof Object) {
    for (const key in source) {
      const newKey = Object.keys(keyMap).includes(key) ? keyMap[key] : key
      res[newKey] = source[key]

      // 是否为深度转换
      if (isDeep && source[key] instanceof Object && Object.keys(source[key]).length) {
        res[newKey] = convertKey(source[key], keyMap, isDeep)
      }
    }
  }
  return res
}

/**
 * 获取数据
 * @param {Object|Array} source 资源
 * @param {Array|String} path 路径  例：user.job.name 或 [‘user’, 'job', 'name']
 * @param {*} defaultValue 默认值
 * @param {Boolean} isDeep 是否为深度
 * @return {*}
 */
export const dGet = (source, path, defaultValue, isDeep) => {
  try {
    if (!source) return defaultValue
    if ((!path || (Array.isArray(path) && path.length === 0))) return defaultValue || source
    var paths = Array.isArray(path) ? path : path.replace(/\[(\d+)\]/g, '.$1').split('.')
    var res = isDeep ? JSON.parse(JSON.stringify(source)) : source
    var toNumber = function (d) {
      return isNaN(Number(d)) ? d : Number(d)
    }
    for (var i = 0, len = paths.length; i < len; i++) {
      var key = toNumber(paths[i])
      res = res[key] // 记录当前的
      if ([undefined].indexOf(res) > -1) {
        return defaultValue
      }
    }
    return res
  } catch (e) {
    return defaultValue
  }
}

export function downloadImg (imgsrc, name) {
  const image = new Image()
  // 解决跨域 Canvas 污染问题s
  image.setAttribute('crossOrigin', 'anonymous')
  image.onload = function () {
    image.width = '100'
    image.height = '100'
    const canvas = document.createElement('canvas')
    canvas.width = image.width
    canvas.height = image.height
    const context = canvas.getContext('2d')
    context.drawImage(image, 0, 0, image.width, image.height)
    const url = canvas.toDataURL('image/png') // 得到图片的base64编码数据
    const a = document.createElement('a') // 生成一个a元素
    const event = new MouseEvent('click')
    a.download = name // 设置图片名称
    a.href = url // 将生成的URL设置为a.href属性
    a.dispatchEvent(event) // 触发a的单击事件
  }
  image.src = imgsrc
}

/**
 * Buffer下载文件
 * @param {Buffer} buffer
 * @param {String} fileName
 */
export const bufferFileDownload = (buffer, fileName) => {
  /* eslint-disable */
  var blob = new Blob([buffer], { type: 'charset=utf-8' })
  var a = document.createElement('a')
  var url = window.URL.createObjectURL(blob)
  a.href = url
  a.download =fileName
  var body = document.getElementsByTagName('body')[0]
  body.appendChild(a)
  a.click()
  body.removeChild(a)
  window.URL.revokeObjectURL(url)
}
/**
 * @description: 根据key查询树的父级
 * @param {*} array 树🌲
 * @param {*} value 值
 * @param {*} valueName key
 * @param {*} childrenName
 * @return {*}
 */
export function parentTree (array, value, valueName = 'value', childrenName = 'children') {
  if (!value || !Array.isArray(array)) return []
  const result = []
  let valid = false
  const seek = (array, value) => {
    let parentValue = ''
    const up = (array, value, lastValue) => {
      array.forEach(v => {
        const val = v[valueName]
        const child = v[childrenName]
        if (val === value) {
          valid = true
          parentValue = lastValue
          return
        }
        if (child && child.length) up(child, value, val)
      })
    }
    up(array, value)
    if (parentValue) {
      result.unshift(parentValue)
      seek(array, parentValue)
    }
  }
  seek(array, value)
  return valid ? [...result, value] : []
}

export const print = function (){
  Array.from(arguments).forEach(item=>{
      if( Object.prototype.toString.call(item).slice(8,-1) === 'Object'){
          let e = [
              `%c ${item.title} %c ${item.content} `, 
              `padding: 1px; border-radius: 3px 0 0 3px; color: #fff; background: #606060;`,
              `padding: 1px; border-radius: 0 3px 3px 0; color: #fff; background: ${item.backgroundColor?item.backgroundColor:'#1475b2'};`
          ]
          let fun = function(){
              return window.console && "function" === typeof window.console.log &&(item = console).log.apply(e, arguments)
          }
          fun.apply(null,e)
      }
  }) 
}

export function calcTime(t) {
  let h = parseInt(Math.floor((t % 86400) / 3600))
  let m = parseInt(parseInt(Math.floor((t % 86400) % 3600) / 60))
  let s = t % 60
  h = h ? h + '时' : ''
  m = m ? m + '分' : ''
  s = s ? s + '秒' : ''
  return h + m + s
}
